<template>
  <div class="evaluation" style="padding-top: 44px;">
    <back-header title="发布评价">
      <span class="release" @click="submit">发布</span>
    </back-header>
    <div class="goods">
      <van-image :src="goods.cover" fit="cover" class="cover" />
      <div class="info">
        <p class="van-ellipsis">{{ goods.title }}</p>
        <span>{{ goods.size }}</span>
      </div>
    </div>
    <div class="star">
      <label>描述相符</label>
      <van-rate void-icon="star" void-color="#D4D4D4" color="#FFD111" size="22px" v-model="rate" />
    </div>
    <textarea @blur.prevent="inputBlur" placeholder="给想买的小伙伴们分享一下商品评价吧~" v-model="desc" class="desc" />
    <div class="imgs">
      <div class="img" v-for="(url, index) in imgs" :key="index" :style="{backgroundImage: `url(${url})`}">
        <img @click="delImg(index)" src="./del@2x.png" alt="">
      </div>
      <img @click="clickFile" class="img" src="./btn_shangchuan@2x.png" alt="">
    </div>
    <input type="file" @change="upload" v-show="false" ref="file">
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import cos from '../../common/js/cos'
import { mapState } from 'vuex'
import { post } from '../../common/js/http'
import { blurInput } from '../../common/js/mixins'

export default {
  name: 'evaluation',
  mixins: [blurInput],
  components: {
    BackHeader
  },
  data() {
    return {
      goods: {},
      rate: 0,
      desc: '',
      imgs: [],
      id: ''
    }
  },
  created() {
    this.goods = this.$route.params.goods
    this.id = this.$route.params.id
  },
  computed: {
    ...mapState(['appConfig'])
  },
  methods: {
    delImg(index) {
      this.imgs.splice(index, 1)
    },
    clickFile() {
      this.$refs.file.click()
    },
    upload() {
      const file = this.$refs.file.files[0]
      if (!file) return
      this.cosImg(file)
        .then((url) => {
          console.log(url)
          this.imgs.push(url)
          this.$refs.file.value = ''
        })
    },
    cosImg(file) {
      const loading = this.$toast.loading({
        message: '上传中...'
      })
      return new Promise((resolve, reject) => {
        const cosObj = cos()
        cosObj.putObject({
          Bucket: this.appConfig.cos_bucket,
          Region: this.appConfig.cos_region,
          Key: this.appConfig.cos_folder_image + `/${file.name}`,
          Body: file
        }, (err, data) => {
          if (err) {
            this.$toast.fail('上传失败')
            reject(err)
            return
          }
          loading.close()
          resolve('https://' + data.Location)
        })
      })
    },
    submit() {
      const loading = this.$toast.loading()
      post('/shop/evaluate/submit', {
        ordergoodsid: this.goods.id,
        goodsid: this.goods.goodsid,
        score: this.rate,
        content: this.desc || '此用户没有填写评价',
        img_urls: this.imgs.join(',')
      })
        .then(() => {
          this.$router.goBack(-1, true)
          loading.close()
        })
        .catch(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
    }
  }
}
</script>

